
/* ==================== common styles ======================= */



*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

a {
  text-decoration: none;
}

img {
  outline: none;
  border: none;
}

section {
  position: relative;
  padding: 20px 0;
}

.sliderWrapper {
  position: relative;
  width: 1920px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.mainSlider {
  position: relative;
  height: 850px;
  background: #f9b00b;
  margin-bottom: 150px;
}

.item {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  align-content: center;
  position: relative;
  width: 100%;
  height: 850px;
  overflow: hidden;
}

.slideBg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
}

.slideContent {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  flex-grow: 1;
  padding: 2%;
}

.slide_4 {
  .slideBg {
      background-image: url(../img/slide4bg.jpg);
    }
}

.slideImages {
  position: relative;
  //outline: 3px solid white;
  //min-width: 50%;
  max-height: 100%;
  margin-right: 10%;

  img {
    max-height: 100%;
  }
}

.slideBack,
.slideMain,
.slideFront {
  transform: translateX(200%);
  //opacity: 0;
}

.owl-item.active {
  .slideBack,
  .slideMain,
  .slideFront {
    transform: translateX(0) !important;
    opacity: 1 !important;
  }
}


.slideBack {
  //outline: 1px solid blue;
  position: absolute;
  top: -10%;
  right: -4%;
  z-index: 2;
  width: auto !important;
  transition: all 1s cubic-bezier(.16,1.07,.27,1.15);
}

.slideMain {
  position: relative;
  //outline: 1px solid purple;
  z-index: 3;
  transition: all 1.5s;
}

.slideFront {
  //outline: 1px solid lime;
  position: absolute;
  top: 70%;
  right: 9%;
  z-index: 4;
  width: auto !important;
  transition: all 3s cubic-bezier(.42,.61,.58,1.37);
}


.info {
  z-index: 100;
}

.mainSlider .item h3 {
  color: white;
  font-size: 90px;
  font-weight: 800;
  line-height: 90px;
  margin-bottom: 20px;
}

.mainSlider .item p {
  color: white;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  width: 240px;
  margin-bottom: 30px;
}

.btn {
  width: 240px;
  height: 70px;
  border-radius: 35px;
  background-color: #4e1e02;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#6b301e), to(#4e1e02));
  background-image: linear-gradient(to bottom, #6b301e 0%, #4e1e02 100%);
  text-align: center;
  color: #ffffff;
  font-family: Geometria;
  font-size: 18px;
  font-weight: 700;
  -webkit-box-shadow: -15px 26px 30px rgba(78, 30, 2, 0.25);
  box-shadow: -15px 26px 30px rgba(78, 30, 2, 0.25);
  border: none;
  display: inline-block;
  cursor: pointer;
}

.mainSlider .item .btn {
  line-height: 70px;
}